.professionalDataForm {
  display: grid;
  align-content: center;
  justify-items: center;
  max-width: 100%;

  form {
    display: grid;
    max-width: 100%;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    width: 100%;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    h2 {
      display: grid;
      grid-row: 1 / 2;
      grid-column: 1 / 9;
      align-items: center;
      justify-content: center;
    }
    label {
      display: grid;
      align-items: center;
      justify-content: center;
    }

    .buttons {
      grid-column: 1 / 9;
      display: flex;
      align-items: center;
      justify-content: center;
      button {
        align-items: center;
        justify-content: center;
        margin: 10px;
        padding: 5px;
        border-radius: 10px;
      }
    }
  }
}
.jobList {
  display: grid;
  grid-template-rows: auto;
  width: 100%;
  align-items: center;
  justify-content: center;
  li {
    display: grid;
    grid-template-columns: 1fr 5fr;
    width: 100%;
    align-items: center;
    justify-content: center;
    h3 {
      grid-column: 1/3;
      grid-row: 1/2;
    }
  }
  .buttons {
    grid-column: 1/3;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  button {
    align-items: center;
    justify-content: center;
    margin: 10px;
    padding: 5px;
    border-radius: 10px;
  }
}
